import axios from 'axios';

const API_URL =
  process.env.NODE_ENV === 'production'
    ? 'https://earls-api.herokuapp.com'
    : '';

const login = async (identity, password) => {
  try {
    const { data } = await axios.post(`${API_URL}/login`, {
      identity,
      password,
    });
    return data;
  } catch (error) {
    return error.message;
  }
};

const fetchAll = async () => {
  const { data } = await axios.get(`${API_URL}/earls`);
  return data;
};

const fetchById = async (earlId) => {
  const { data } = await axios.get(`${API_URL}/earls/${earlId}`);
  return data;
};

const createEarl = async (target) => {
  const { data } = await axios.post(`${API_URL}/earls`, {
    target,
  });
  return data;
};

export { login, fetchAll, fetchById, createEarl };
